/* Header.css */

.header {
    background-color: #282c34;
    color: white;
    padding: 20px;
    text-align: center;
  }
  
  .nav {
    margin: 10px 0;
  }
  
  .social-link, .about-link {
    color: #61dafb;
    text-decoration: none;
    margin: 0 15px;
    font-size: 16px;
  }
  
  .social-link:hover, .about-link:hover {
    text-decoration: underline;
  }
  
  .start-button {
    background-color: #61dafb;
    border: none;
    color: white;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    margin-top: 10px;
  }
  
  .start-button:hover {
    background-color: #21a1f1;
  }
  